import React from 'react'
import styled from 'styled-components'
import img2 from './images/img2.png'
import divider from './images/divider.png'

export const About = () => {
    return (
        <Container id="despre">
            <LeftSide>
                <Img src={img2} alt="salon denisa masaj timisoara"/>
            </LeftSide>
            <RightSide>
                <Title>Despre serviciile noastre</Title>
                <Subtitle>Terapii medicale i de relaxare</Subtitle>
                <DescWrap>
                    <Desc><u>Masajul terapeutic</u> - reprezint un mijloc eficient de prevenire, ameliorare i tratare a numeroaselor afeciuni musculare i articulare, mbunatind circulaia sanguin i drenajul limfatic.
                    <br/><br/>
                    Tehnicile de masaj sunt indicate mai ales n afeciunile aparatului locomotor (reumatismale, neurologice, posttraumatice, afeciuni articulare i periarticulare). De asemenea, este cunoscut faptul c masajul are efecte benefice asupra organismului, favoriznd relaxarea fizic dar i psihic.
                    </Desc>
                    <Desc><u>Masajul de Relaxare</u> - Cel mai popular masaj adaptat nevoilor corpului tu i care ajut la eliminarea toxinelor, elibereaz mintea de stres i rsfa pielea cu uleiuri aromatice.
                    <br/><br/>
                    Tehnicile de masaj sunt realizate n acompaniamentul meloterapiei.
                    <br/><br/>
                    Rezultatul scontat presupune stimularea fluxului de snge i oxigen care aduc corpul ntr-un echilibru  fizic dar i mental.
                    </Desc>
                </DescWrap>
                <Divider src={divider} alt="divider"/>
            </RightSide>
        </Container>
    )
}


const Container = styled.article`
    min-height: 700px;
    background-color: #f5f5f5;
    display: flex;
    align-items: center;
    padding: 10vw;
    gap: 5vw;
    @media screen and (max-width: 800px) {
        flex-direction: column;   
        padding: 5vw;
    }
`
const LeftSide = styled.section`
    flex: 1;
    height: 100%;
`
const Img = styled.img`
    filter: drop-shadow(-10px 10px 20px lightgray);
    max-width: 40vw;
`
const RightSide = styled.section`
    flex: 1;
    display: flex;
    align-items: center;
    flex-direction: column;
`
const Title = styled.h2`
    font-size: var(--mf);
    /* font-family: var(--fsr); */
    font-weight: 400;
    margin-bottom: var(--sf);
`
const Subtitle = styled.h3`
    font-weight: 400;
    margin-bottom: var(--xsf);
    font-size: var(--xsf);
    color: gray;
    font-style: italic;
`
const DescWrap = styled.article`
    display: flex;
    gap: 50px;
    width: 100%;
    @media screen and (max-width: 1100px) {
        flex-direction: column;   
    }
`
const Desc= styled.p`
    text-align: justify;
    flex: 1;
    line-height: 1.5;
`
const Divider = styled.img`
    width: 200px;
    text-align: center;
`